.charts-card {
	width: 16.25rem;
	height: 16.25rem;
	border-radius: 5px;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.0212249);
}

.charts-card.average-sessions {
    background-color: #ff0000;
}


.charts-card.performance {
    background-color: #282d30;
}

.charts-card.score {
    background-color: #fbfbfb;
}