
.sidebar {
	z-index: 1;
	position: fixed;
	top: 0;
	left: 0;
	width: 7.3125rem;
	height: 100%;
	overflow: hidden;
	background: #020203;
	text-align: center;
}

.sidebar-button-wrapper {
  margin: 0;
}

.copyright {
	position: absolute;
	bottom: 5rem;
	left: -8%;
	width: max-content;
	margin: 0;
	transform: rotate(-90deg);
	color: #ffffff;
	font-family: 'Roboto';
	font-size: 0.75rem;
	font-weight: 500;
}

.sidebar-button {
	width: 4rem;
	height: 4rem;
	margin-bottom: 1.25rem;
	border: none;
	border-radius: 6px;
	background-color: #ffffff;
}

.sidebar-button -logo {
  width: 2rem;
  height: 2rem;
}